import stringSimilarity from "string-similarity"

export const compareStrings = (a, b) => {
  const similarity = stringSimilarity.compareTwoStrings(b.toLowerCase(), a.toLowerCase())
  console.log(similarity)
  return similarity
}

export const shiftChars = (str, amount) => {
  return str
    .split("")
    .map((a) => String.fromCharCode(a.charCodeAt(0) + amount))
    .join("")
}

export const encodeObj = (obj) => encode(JSON.stringify(obj))
export const decodeObj = (str) => JSON.parse(decode(str))

export const encode = (str) => {
  str = shiftChars(str, -1)
  return encodeURIComponent(str)
}

export const decode = (str) => {
  str = decodeURIComponent(str)
  return shiftChars(str, 1)
}

export const copyToClipboard = (str) => {
  const el = document.createElement("textarea")
  el.value = str
  document.body.appendChild(el)
  el.select()
  document.execCommand("copy")
  document.body.removeChild(el)
}
